import { SceneComponentProps } from "@grafana/scenes";
import React from "react";
import { NotificationContainer, NotificationManager } from 'react-notifications';

export function Notifications({ model }: SceneComponentProps<any>) {
    // console.log(model);

    model.createNotification = (type, message, title, delay) => {
        return () => {
            switch (type) {
                case 'info':
                    NotificationManager.info(message, title, delay);
                    break;
                case 'success':
                    NotificationManager.success(message, title, delay);
                    break;
                case 'warning':
                    NotificationManager.warning(message, title, delay);
                    break;
                case 'error':
                    NotificationManager.error(message, title, delay);
                    break;
            }
        };
    };

    return (
        <NotificationContainer />
    );
}
