import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { Menu, Pagination, Input } from '@grafana/ui';
import { SceneComponentProps } from '@grafana/scenes';

export function Sites({ model }: SceneComponentProps<any>) {
  // console.log(model);

  const [sites, setSites] = useState<any>();
  model.setSites = setSites;

  return (
    <div className={mainDiv}>
      <h6>New Sites</h6>
      <Input placeholder='Search site by ATollID/Bs Number/Location Name' onKeyDown={(e) => {
            if (e.key === 'Enter') {
              model.searchTerm = (e.target as any).value;                        
              model.updateAll(1);            
            }
          }} />
      <Menu id='sites'>
        {sites?.content?.map((site, i) => {
          return <Menu.Item label={site} key={i} active={site === model.selectedSite} onClick={() => {
            model.updateNewLocReqsForSite(site, 1);
            model.updateProcessedLocReqsForSite(site, 1);
            model.updateInvalidLocReqsForSite(site, 1);
          }} />
        })}
      </Menu>
      <Pagination
        className="sitesPagination"
        currentPage={sites?.pageable?.pageNumber + 1}
        numberOfPages={sites?.totalPages}
        onNavigate={(p) => model.updateAll(p)} />
    </div>
  );
}
