import React, { useEffect } from 'react';
import { getMainSplitScene } from './ApprovalAppScene';
import { LocReqService } from 'services/LocReqService';
import { UserService } from 'services/UserService';
import { ErrorPopup } from './Approval/ErrorPopup';
import { Notifications } from './Approval/Notifications';
import { Loader } from './Approval/Loader';

export const ApprovalApp = (state: any) => {
  // const state: any = {};
  // console.log(state);

  state.getUser = () => {
    UserService.getUser(state.grafanaRootUrl)
      .then((data) => {
        state.userEmail = data[0].email;
      });
  };

  state.updateMarkers = (locReq: any) => {
    const userLocatiion = { lat: Number(locReq.userLatitude), lng: Number(locReq.userLongitude) };
    const siteLocatiion = { lat: Number(locReq.siteLatitude), lng: Number(locReq.siteLongitude) };
    state.setMarkers([

      {
        id: 1,
        name: "Site Location",
        position: siteLocatiion,
        icon: "http://maps.google.com/mapfiles/ms/icons/red-dot.png"
      },
      {
        id: 2,
        name: "User Location",
        position: userLocatiion,
        icon: "http://maps.google.com/mapfiles/ms/icons/red-dot.png"
      }
    ]);
    state.setActiveMarker(1);
  };

  state.updateNewLocReqsForSite = (site: string, page: number) => {
    state.selectedSite = site;
    state.setLoaderVisible(true);
    LocReqService.getLocReqs(state.grafanaRootUrl, state.backendPluginId, site, 'new', page - 1, 10)
      .then((data) => {
        state.setLoaderVisible(false);
        state.setNewLocationRequests(data);
        state.setLocationRequest(data.content[0]);
        state.setPictureUrl(data.content[0].pictureUrl);
        state.updateMarkers(data.content[0]);
        state.setRadiusMarker([]);
        state.setDistanceInMeters('');
        state.setSelectedLocReq(data.content[0].id);
        state.locationRequestsCount = data.numberOfElements;
      });
  };

  state.updateProcessedLocReqsForSite = (site: string, page: number) => {
    state.selectedSite = site;
    state.setLoaderVisible(true);
    LocReqService.getLocReqs(state.grafanaRootUrl, state.backendPluginId, site, 'processed', page - 1, 10)
      .then((data) => {
        state.setLoaderVisible(false);
        state.setProcessedLocationRequests(data);
      });
  }

  state.updateInvalidLocReqsForSite = (site: string, page: number) => {
    state.selectedSite = site;
    state.setLoaderVisible(true);
    LocReqService.getLocReqs(state.grafanaRootUrl, state.backendPluginId, site, 'invalid', page - 1, 10)
      .then((data) => {
        state.setLoaderVisible(false);
        state.setInvalidLocationRequests(data);
      });
  }

  state.updateAll = (page: number) => {
    state.setLoaderVisible(true);

    const searchTerm = state.searchTerm !== undefined ? state.searchTerm : '';

    LocReqService.getSites(state.grafanaRootUrl, state.backendPluginId, searchTerm, page - 1, 10)
      .then((data) => {
        state.setLoaderVisible(false);
        state.setSites(data);
        console.log(data.numberOfElements);
        console.log('v1');
        if (data.numberOfElements > 0) {
          state.updateNewLocReqsForSite(data.content[0], 1);
          state.updateProcessedLocReqsForSite(data.content[0], 1);
          state.updateInvalidLocReqsForSite(data.content[0], 1);
        } else {          
          state.setLocationRequest(null);
          state.setPictureUrl(null);
          state.setMarkers([]);
          state.setRadiusMarker([]);
          state.setDistanceInMeters('');
          state.setSelectedLocReq(null);
          state.setNewLocationRequests(data);
          state.setProcessedLocationRequests(data);
          state.setInvalidLocationRequests(data);
          state.setLatitude(null);
          state.setLongitude(null);
        }
      });
  };

  state.updateLocReq = (id: string, lat: string, lng: string, redius: number) => {
    state.setLoaderVisible(true);
    LocReqService.updateLocReq(state.grafanaRootUrl, state.backendPluginId, id, lat, lng, redius, state.userEmail)
      .then((data) => {
        state.setLoaderVisible(false);
        if (!data.errorMessage) {
          state.createNotification('info', 'Info', 'Location request updated successfully.', 5000);
          // if (state.locationRequestsCount === 1) {
            state.updateAll(1);
          // }
          // else {
            // state.updateNewLocReqsForSite(state.selectedSite, 1);
          // }
        } else {
          state.showErrorPopup(data.errorMessage);
        }
      });
  }

  useEffect(() => {    
    state.getUser();
    state.updateAll(1);    
  });

  const scene = getMainSplitScene(state);

  return (
    <div className='approvalAppContainer'>
      <ErrorPopup model={state}></ErrorPopup>
      <Notifications model={state}></Notifications>
      <Loader model={state}></Loader>
      <scene.Component model={scene} />
    </div>
  );
};
