import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { SceneComponentProps } from '@grafana/scenes';

export function LocationPhoto({ model }: SceneComponentProps<any>) {
    // console.log(model);

    const [pictureUrl, setPictureUrl] = useState<any>();
    model.setPictureUrl = setPictureUrl;

    return (
        <div className={mainDiv} style={{ textAlign: 'center' }}>
            <h6>Location Picture</h6>
            {pictureUrl ?
                <img src={pictureUrl} style={{ position: 'relative', top: '50%', transform: 'translateY(-50%)', maxWidth: '100%', maxHeight: '100%' }}></img>
                : <div style={{ position: 'relative', top: '50%', transform: 'translateY(-50%)', fontSize: '100px', fontWeight: 'bold' }}>NA</div>}
        </div>
    );
}
