import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { Circle, GoogleMap, InfoWindow, Marker, Polyline, useJsApiLoader } from '@react-google-maps/api';
import { SceneComponentProps } from '@grafana/scenes';
import { Switch } from '@grafana/ui';

export function LocationGeoMap({ model }: SceneComponentProps<any>) {
  // console.log(model);
  let { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: "AIzaSyBs0IhdT9Fn0OkrJSonjjyQ7fbjfHjJuz8"
  });

  const [refresh, setRefresh] = useState(0);
  const [map, setMap] = useState<any>();
  const [markers, setMarkers] = useState<any>([]);
  const [radiusMarkers, setRadiusMarker] = useState<any>([]);
  const [activeMarker, setActiveMarker] = useState<any>(1);
  const [markerSwitch, setMarkerSwitch] = useState<any>(false);
  const [radiusManualMode, setRadiusManualMode] = useState<any>(false);

  model.setMarkers = setMarkers;
  model.setActiveMarker = setActiveMarker;
  model.setRadiusMarker = setRadiusMarker;
  model.setRadiusManualMode = setRadiusManualMode;
  model.refreshLocationGeoMap = () => { setRefresh(refresh + 1); };

  const onMapClick = (e) => {
    if (markerSwitch) {
      markers[2] = {
        id: 3,
        name: "New Location",
        position: {
          lat: e.latLng.lat(),
          lng: e.latLng.lng()
        },
        icon: "http://maps.google.com/mapfiles/ms/icons/purple-dot.png"
      };

      setMarkers(markers);
    } else {
      radiusMarkers[0] = {
        id: 3,
        name: "Radius Location",
        position: {
          lat: e.latLng.lat(),
          lng: e.latLng.lng()
        },
        icon: "http://maps.google.com/mapfiles/ms/icons/green-dot.png"
      };

      setRadiusMarker(radiusMarkers);
      if (radiusManualMode) {
        setRadiusManualMode(false);
      }
    }

    setRefresh(refresh + 1);
  };

  const updateActiveMarker = (index) => {
    setActiveMarker(index);
    map.setCenter(markers[activeMarker].position);
    map.setZoom(15);
    setRefresh(refresh + 1);
  };

  if (markers.length >= 1) {
    model.setLatitude(markers[activeMarker].position.lat);
    model.setLongitude(markers[activeMarker].position.lng);
  }

  if (!markerSwitch && !radiusManualMode && radiusMarkers.length === 1 && google.maps.geometry) {
    const distanceInMeters = google.maps.geometry.spherical.computeDistanceBetween(markers[activeMarker].position, radiusMarkers[0].position);
    model.distanceInMeters = distanceInMeters;
    model.setDistanceInMeters(distanceInMeters);
  }

  return (<div className={mainDiv}>
    {isLoaded && markers.length > 0 ? <div className="radiusManualModeSwitch"><Switch value={markerSwitch} onChange={(s) => {
      setMarkerSwitch((s.target as any).checked)
    }} /></div> : <></>}
    {isLoaded && markers.length > 0 ? (
      <GoogleMap
        mapContainerStyle={{ width: '100%', height: '100%' }}
        center={markers[activeMarker].position}
        zoom={15}
        onClick={onMapClick}
        onLoad={(map) => { setMap(map) }}
        options={{ minZoom: 4, maxZoom: 24 }}
      >
        {markers.map(({ id, name, position, icon }, index) => (
          <Marker
            key={id}
            position={position}
            icon={icon}
            onClick={() => { updateActiveMarker(index) }}
          >
            {/* {index === activeMarker && name ? */}
            <InfoWindow position={position} options={{ pixelOffset: new window.google.maps.Size(0, id === 3 ? -32 : -0) }}>
              <div>{name}</div>
            </InfoWindow>
            {/* : <></>} */}
          </Marker>
        ))}
        {!radiusManualMode ? radiusMarkers.map(({ id, name, position, icon }, index) => (
          <Marker
            key={id}
            position={position}
            icon={icon}
            onClick={() => {
              setRadiusMarker([]);
              model.setDistanceInMeters('');
            }}
          >
            {/* {index === activeMarker && name ? */}
              <InfoWindow position={position} options={{ pixelOffset: new window.google.maps.Size(0, -32) }}>
                <div>{name}</div>
              </InfoWindow>
              {/* : <></>} */}
          </Marker>
        )) : <></>}
        {!markerSwitch && !radiusManualMode && radiusMarkers.length === 1 ?
          <Polyline path={[markers[activeMarker].position, radiusMarkers[0].position]}></Polyline>
          : <></>}
        {!markerSwitch && !radiusManualMode && radiusMarkers.length === 1 ?
          <Circle center={markers[activeMarker].position} radius={model.distanceInMeters}></Circle>
          : <></>}
        {!markerSwitch && radiusManualMode ?
          <Circle center={markers[activeMarker].position} radius={model.distanceInMeters}></Circle>
          : <></>}
        <></>
      </GoogleMap>
    ) : <></>}
  </div>)
}
