export const LocReqService = {
    getSites: async (grafanaRootUrl: string, backendPluginId: string, searchTerm: string, page: number, size: number) => {
        const response = await fetch(`${grafanaRootUrl}/api/datasources/proxy/uid/${backendPluginId}/locreq/sites/new?page=${page}&size=${size}&searchTerm=${searchTerm}`);
        return await response.json();
    },

    getLocReqs: async (grafanaRootUrl: string, backendPluginId: string, aTollID: string, status: string, page: number, size: number) => {
        const response = await fetch(`${grafanaRootUrl}/api/datasources/proxy/uid/${backendPluginId}/locreq/get/${aTollID}/${status}?page=${page}&size=${size}`);
        return await response.json();
    },

    updateLocReq: async (grafanaRootUrl: string, backendPluginId: string, id: string, lat: string, lng: string, redius: number, verifiedBy: string) => {
        try {
            const response = await fetch(`${grafanaRootUrl}/api/datasources/proxy/uid/${backendPluginId}/locreq/${id}`, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ 'latitude': String(lat), 'longitude': String(lng), 'radius': redius, 'verifiedBy': verifiedBy })
            });

            return await response.json();
        } catch (err) {
            return err;
        }
    }
};
