import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { Form, Field, Input, Button } from '@grafana/ui';
import { SceneComponentProps } from '@grafana/scenes';

export function VerifiedLocationForm({ model }: SceneComponentProps<any>) {
  // console.log(model);

  const [latitude, setLatitude] = useState<any>();
  const [longitude, setLongitude] = useState<any>();
  const [distanceInMeters, setDistanceInMeters] = useState<any>();
  const [selectedLocReq, setSelectedLocReq] = useState<any>();

  model.setLatitude = setLatitude;
  model.setLongitude = setLongitude;
  model.setDistanceInMeters = setDistanceInMeters;
  model.setSelectedLocReq = (locReqId) => {
    model.selectedLocReq = locReqId;
    setSelectedLocReq(locReqId);
  };

  return (
    <div className={mainDiv}>
      <h6>Submit Verified Location Details for {selectedLocReq}</h6>
      <Form onSubmit={(data) => {
        model.updateLocReq(selectedLocReq, latitude, longitude, distanceInMeters);
      }}>{({ register, errors }) => {
        return (
          <>
            <Field>
              <Input placeholder='Latitude' value={latitude} readOnly />
            </Field>
            <Field>
              <Input placeholder='Longitude' value={longitude} readOnly />
            </Field>
            <Field>
              <Input autoFocus placeholder='Radius' key={distanceInMeters} defaultValue={distanceInMeters} type='number' onChange={(e) => {
                setDistanceInMeters(Number((e.target as any).value));
                model.distanceInMeters = Number((e.target as any).value);
                model.setRadiusManualMode(true);
                model.refreshLocationGeoMap();
              }} />
            </Field>
            <Field>
              <Button disabled={!selectedLocReq || !latitude || !longitude || !distanceInMeters || !model.userEmail} type="submit">Submit</Button>
            </Field>
          </>
        )
      }}</Form>
    </div>
  );
}
