import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { Menu, Pagination } from '@grafana/ui';
import { SceneComponentProps } from '@grafana/scenes';

export function LocationRequests({ model }: SceneComponentProps<any>) {
  // console.log(model);

  const [newLocationRequests, setNewLocationRequests] = useState<any>();
  model.setNewLocationRequests = setNewLocationRequests;

  const [processedLocationRequests, setProcessedLocationRequests] = useState<any>();
  model.setProcessedLocationRequests = setProcessedLocationRequests;

  const [invalidLocationRequests, setInvalidLocationRequests] = useState<any>();
  model.setInvalidLocationRequests = setInvalidLocationRequests;

  return (
    <div className={mainDiv}>
      {newLocationRequests?.totalPages > 0 ?
        <div>
          <h6>New Location Requests</h6>
          <Menu id='newLocationRequests'>
            {newLocationRequests?.content?.map((locReq, i) => {
              return <Menu.Item label={locReq.id} key={i} active={locReq.id === model.selectedLocReq} onClick={() => {
                model.setLocationRequest(locReq);
                model.setRadiusMarker([]);
                model.updateMarkers(locReq);
                model.setSelectedLocReq(locReq.id);
                model.setDistanceInMeters('');
                model.setPictureUrl(locReq.pictureUrl);
              }} />
            })}
          </Menu>
          <Pagination
            className="newLocationRequestsPagination"
            currentPage={newLocationRequests?.pageable?.pageNumber + 1}
            numberOfPages={newLocationRequests?.totalPages}
            onNavigate={(p) => model.updateNewLocReqsForSite(model.selectedSite, p)}
          /> </div> : <></>}

      {processedLocationRequests?.totalPages > 0 ?
        <div>
          <h6 style={{ marginTop: '60px' }}>Processed Location Requests</h6>
          <Menu id='processedLocationRequests'>
            {processedLocationRequests?.content?.map((locReq, i) => {
              return <Menu.Item label={locReq.id} key={i} active={locReq.id === model.selectedLocReq} onClick={() => {
                model.setLocationRequest(locReq);
                model.setRadiusMarker([]);
                model.updateMarkers(locReq);
                model.setSelectedLocReq(locReq.id);
                model.setDistanceInMeters('');
                model.setPictureUrl(locReq.pictureUrl);
              }} />
            })}
          </Menu>
          <Pagination
            className="processedLocationRequestsPagination"
            currentPage={processedLocationRequests?.pageable?.pageNumber + 1}
            numberOfPages={processedLocationRequests?.totalPages}
            onNavigate={(p) => model.updateProcessedLocReqsForSite(model.selectedSite, p)}
          /> </div> : <></>}

      {invalidLocationRequests?.totalPages > 0 ?
        <div>
          <h6 style={{ marginTop: '60px' }}>Invalid Location Requests</h6>
          <Menu id='invalidLocationRequests'>
            {invalidLocationRequests?.content?.map((locReq, i) => {
              return <Menu.Item label={locReq.id} key={i} active={locReq.id === model.selectedLocReq} onClick={() => {
                model.setLocationRequest(locReq);
                model.setRadiusMarker([]);
                model.updateMarkers(locReq);
                model.setSelectedLocReq(locReq.id);
                model.setDistanceInMeters('');
                model.setPictureUrl(locReq.pictureUrl);
              }} />
            })}
          </Menu>
          <Pagination
            className="invalidLocationRequestsPagination"
            currentPage={invalidLocationRequests?.pageable?.pageNumber + 1}
            numberOfPages={invalidLocationRequests?.totalPages}
            onNavigate={(p) => model.updateInvalidLocReqsForSite(model.selectedSite, p)}
          /> </div> : <></>}
    </div>
  );
}
