import { SceneComponentProps } from "@grafana/scenes";
import React, { useState } from "react";
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

export function ErrorPopup({ model }: SceneComponentProps<any>) {
    // console.log(model);

    const [open, setOpen] = useState<any>(false);
    const [errorContent, setErrorContent] = useState<any>();

    model.showErrorPopup = (errorContent) => {
        setErrorContent(errorContent);
        setOpen(true);
    };

    return (
        <Popup open={open} closeOnDocumentClick={false} closeOnEscape={false}>
            <h6>Error</h6>
            <hr />
            <div style={{ fontWeight: 'bold' }}>{errorContent ? errorContent.substring(0, errorContent.indexOf("[")).trim() : ''}</div>
            <div style={{ fontSize: 'smaller' }}>[{errorContent ? errorContent.substring(errorContent.indexOf("id:") + 3, errorContent.indexOf("]")).trim() : ''}]</div>
            <hr />
            <button onClick={() => setOpen(false)}>OK</button>
        </Popup>
    );
}
