import React, { useState } from 'react';
import { mainDiv } from '../../constants';
import { Card } from '@grafana/ui';
import { SceneComponentProps } from '@grafana/scenes';

export function LocationRequestDetails({ model }: SceneComponentProps<any>) {
  // console.log(model);

  const [locationRequest, setLocationRequest] = useState<any>();   
  model.setLocationRequest = setLocationRequest; 

  return (
    <div className={mainDiv}>
      <h6>Location Request Details</h6>
      <Card>
        <Card.Heading>{locationRequest?.id}</Card.Heading>
        <Card.Description>
          Id Number: {locationRequest?.idNumber}<br />
          Given Name: {locationRequest?.givenName}<br />
          Family Name: {locationRequest?.familyName}<br />
          Telephone Number: {locationRequest?.telephoneNumber}<br />          
          ATollID: {locationRequest?.atollID}<br />
          BsNumber: {locationRequest?.bsNumber}<br />
          Location Name: {locationRequest?.locationName}<br />
          User Latitude: {locationRequest?.userLatitude}<br />
          User Longitude: {locationRequest?.userLongitude}<br />
          User Location Accuracy: {locationRequest?.accuracy}<br />
          Site Latitude: {locationRequest?.siteLatitude}<br />
          Site Longitude: {locationRequest?.siteLongitude}<br />
          Site Radius: {locationRequest?.siteRadius}<br />
          Status: {locationRequest?.status}<br />
          Lock: {locationRequest?.lock}<br />
          Timestamp: {locationRequest?.createDateTime}
        </Card.Description>
      </Card>
    </div>
  );
}
