import { SceneComponentProps } from "@grafana/scenes";
import React, { useState } from "react";
import { Circles } from 'react-loader-spinner'

export function Loader({ model }: SceneComponentProps<any>) {
    // console.log(model);

    const [loaderVisible, setLoaderVisible] = useState<any>(false);
    model.setLoaderVisible = setLoaderVisible;

    return (
        <Circles
            height="80"
            width="80"
            color="white"
            ariaLabel="circles-loading"
            wrapperStyle={{}}
            wrapperClass="appLoader"
            visible={loaderVisible}
        />
    );
}
