
import { EmbeddedScene, SceneFlexItem, SceneReactObject, SplitLayout } from '@grafana/scenes';
import { Sites } from './Approval/Sites';
import { LocationRequests } from './Approval/LocationRequests';
import { LocationGeoMap } from './Approval/LocationGeoMap';
import { LocationRequestDetails } from './Approval/LocationRequestDetails';
import { VerifiedLocationForm } from './Approval/VerifiedLocationForm';
import { LocationPhoto } from './Approval/LocationPhoto';

export function getChildSplitScene4(state) {
  return new SplitLayout({
    direction: 'row',
    primary: new SceneFlexItem({ body: new SceneReactObject({ component: VerifiedLocationForm, props: { model: state } }) }),
    secondary: new SceneFlexItem({ body: new SceneReactObject({ component: LocationPhoto, props: { model: state } }) }),
  });
}

export function getChildSplitScene2(state) {
  return new SplitLayout({
    direction: 'row',
    initialSize: 0.3,
    primary: new SceneFlexItem({ body: new SceneReactObject({ component: LocationRequestDetails, props: { model: state } }) }),
    secondary: new SceneFlexItem({ body: new SceneReactObject({ component: LocationGeoMap, props: { model: state } }) }),
  });
}

export function getChildSplitScene3(state) {
  return new SplitLayout({
    direction: 'row',
    initialSize: 0.3,
    primary: new SceneFlexItem({ body: new SceneReactObject({ component: LocationRequests, props: { model: state } }) }),
    secondary: getChildSplitScene4(state),
  });
}

export function getChildSplitScene1(state) {
  return new SplitLayout({
    direction: 'column',
    initialSize: 0.65,
    primary: getChildSplitScene2(state),
    secondary: getChildSplitScene3(state),
  });
}

export function getMainSplitScene(state) {
  return new EmbeddedScene({
    body: new SplitLayout({
      direction: 'row',
      initialSize: 0.15,
      primary: new SceneFlexItem({ body: new SceneReactObject({ component: Sites, props: { model: state } }) }),
      secondary: getChildSplitScene1(state),
    }),
  });
}
