import React, { ChangeEvent, useState } from 'react';
import { lastValueFrom } from 'rxjs';
import { css } from '@emotion/css';
import { AppPluginMeta, GrafanaTheme2, PluginConfigPageProps, PluginMeta } from '@grafana/data';
import { getBackendSrv } from '@grafana/runtime';
import { Button, Field, FieldSet, Input, useStyles2 } from '@grafana/ui';

export type AppPluginSettings = {
  grafanaRootUrl: string,
  backendPluginId: string;
};

export interface AppConfigProps extends PluginConfigPageProps<AppPluginMeta<any>> { }

export const AppConfig = ({ plugin }: AppConfigProps) => {
  const s = useStyles2(getStyles);
  const { enabled, pinned, jsonData } = plugin.meta;
  const [state, setState] = useState<AppPluginSettings>({
    grafanaRootUrl: jsonData?.grafanaRootUrl || '',
    backendPluginId: jsonData?.backendPluginId || '',
  });

  const onChange = (event: ChangeEvent<HTMLInputElement>) => {
    setState({
      ...state,
      [event.target.name]: event.target.value.trim(),
    });
  };

  return (
    <div>
      <FieldSet label="API Settings">

        <Field label="Grafana Root URL" description="" className={s.marginTop}>
          <Input
            width={60}
            name="grafanaRootUrl"
            value={state.grafanaRootUrl}
            placeholder="Grafana Root URL"
            onChange={onChange}
          />
        </Field>

        <Field label="Backend plugin ID" description="" className={s.marginTop}>
          <Input
            width={60}
            name="backendPluginId"
            value={state.backendPluginId}
            placeholder="Backend plugin ID"
            onChange={onChange}
          />
        </Field>

        <div className={s.marginTop}>
          <Button
            type="submit"
            onClick={() =>
              updatePluginAndReload(plugin.meta.id, {
                enabled,
                pinned,
                jsonData: {
                  grafanaRootUrl: state.grafanaRootUrl,
                  backendPluginId: state.backendPluginId,
                }
              })
            }
            disabled={Boolean(!state.grafanaRootUrl || !state.backendPluginId)}
          >
            Save settings
          </Button>
        </div>
      </FieldSet>
    </div>
  );
};

const getStyles = (theme: GrafanaTheme2) => ({
  marginTop: css`
    margin-top: ${theme.spacing(3)};
  `,
});

const updatePluginAndReload = async (pluginId: string, data: Partial<PluginMeta<AppPluginSettings>>) => {
  try {
    await updatePlugin(pluginId, data);

    // Reloading the page as the changes made here wouldn't be propagated to the actual plugin otherwise.
    // This is not ideal, however unfortunately currently there is no supported way for updating the plugin state.
    window.location.reload();
  } catch (e) {
    console.error('Error while updating the plugin', e);
  }
};

export const updatePlugin = async (pluginId: string, data: Partial<PluginMeta>) => {
  const response = await getBackendSrv().fetch({
    url: `/api/plugins/${pluginId}/settings`,
    method: 'POST',
    data,
  });

  return lastValueFrom(response);
};
